import React, { useEffect, useRef, useState } from "react";
import { useLocation } from "react-router-dom";
import Page from "../components/Page.jsx";
import { gsap } from "gsap";
import NavBar from "../components/Nav.jsx";
import AsideNav from "../components/Aside.jsx";
import Footer from "../components/Footer.jsx";
import PartnersShipData from '../json/pages/About.json';

import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination } from "swiper";
import "swiper/css";
import "swiper/css/pagination";

import aboutBackImg from '../assets/imgs/about/about_visual.png';
import aboutScaleImg from '../assets/imgs/about/about_scale.png';
import awardSvg from '../assets/imgs/about/award.svg';
import aboutVideo from '../assets/video/lgcns_service_offering.mp4';



const AboutPage = () => {
  const circleRef = useRef(null);
  const { pathname } = useLocation() 

  const handleSlideChange = () => {
    if (circleRef.current) {
      gsap.to(circleRef.current, {
        rotation: "+=360",
        duration: 1.5,
        ease: "power2.inOut",
      });
    }
  };

  useEffect(() => {
    gsap.utils.toArray("section").forEach((section) => {
      gsap.fromTo(
        section,
        { opacity: 0, y: 100 },
        {
          opacity: 1,
          y: 0,
          scrollTrigger: {
            trigger: section,
            start: "top 30%",
            end: "bottom top",
            onEnter: () => section.classList.add("is_current"),
            onLeave: () => section.classList.remove("is_current"),
            onEnterBack: () => section.classList.add("is_current"),
            onLeaveBack: () => section.classList.remove("is_current"),
            toggleActions: "play none none reverse",
          },
        }
      );
    });
  }, []);

  const [activeIndex, setActiveIndex] = useState(0);

  const keywordSlideChange = (swiper) => {
    setActiveIndex(swiper.realIndex);
  };

  const [aboutMediaLoaded, setAboutMediaLoaded] = useState(false);
  useEffect(() => {
    if (aboutMediaLoaded) {
      window.scrollTo(0, 0);
    }
  }, [aboutMediaLoaded]);

  useEffect(() => {
    const aboutHandleLoad = () => setAboutMediaLoaded(true);

    window.addEventListener("load", aboutHandleLoad);

    return () => window.removeEventListener("load", aboutHandleLoad);
  }, []);

  return (
    <div className="aboutPage">
      <Page header={<NavBar paths={pathname}/>} aside={<AsideNav pathName={pathname}/>} footer={<Footer />}>
        <section
          id="aboutVisualWrap"
          style={{
            backgroundImage: `url(${aboutBackImg})`,
          }}
        >
          <strong>
            About <br />
            LG CNS CLOUD
          </strong>
          <p>
            LG CNS는 약속합니다
            <sub>
              최적화된 클라우드 서비스로 고객의 가치를 <br className="br_600" /> 성장시키고 디지털 혁신의 꽃을 피워 가겠습니다.
            </sub>
          </p>
        </section>

        <section
          id="aboutContent1"
          style={{
            backgroundImage: `url(${aboutScaleImg})`,
          }}
        >
          <div className="keywordWrap">
            <p className="left">
              <i className={activeIndex === 0 ? "is_active" : ""}></i>
              <i className={activeIndex === 1 ? "is_active" : ""}></i>
              <i className={activeIndex === 2 ? "is_active" : ""}></i>
            </p>
            <Swiper
              spaceBetween={20}
              slidesPerView={1}
              centeredSlides={true}
              direction={"vertical"}
              loop={true}
              speed={800}
              autoplay={{
                delay: 2000,
                disableOnInteraction: false,
              }}
              modules={[Autoplay, Pagination]}
              onSlideChange={keywordSlideChange}
            >
              <SwiperSlide>
                <strong>Future Innovator</strong>
                <sub>압도적인 기술과 인프라</sub>
              </SwiperSlide>
              <SwiperSlide>
                <strong>Global Tospan Tier</strong>
                <sub>풍부한 경험과 노하우</sub>
              </SwiperSlide>
              <SwiperSlide>
                <strong>Trusted spanartner</strong>
                <sub>믿음직한 통합서비스</sub>
              </SwiperSlide>
            </Swiper>
            <p className="right">
              <i className={activeIndex === 0 ? "is_active" : ""}></i>
              <i className={activeIndex === 1 ? "is_active" : ""}></i>
              <i className={activeIndex === 2 ? "is_active" : ""}></i>
            </p>
          </div>
        </section>
        <section id="aboutContent2">
          <div className="leftWrap">
            <div className="circle" ref={circleRef}></div>
            <span>
              Our <mark>Value</mark>
            </span>
            <p>
              30년 넘게 대한민국 IT산업의 발전을 이끌어 오면서 쌓아온 <br />
              전문성과 혁신성을 그대로 고객의 클라우드 서비스에 반영하여 고객의 가치를 한층 더 높이겠습니다.
            </p>
          </div>

          <div className="swiperWrap">
            <Swiper
              spaceBetween={20}
              slidesPerView={3}
              centeredSlides={true}
              direction={"vertical"}
              loop={true}
              speed={800}
              autoplay={{
                delay: 2000,
                disableOnInteraction: false,
              }}
              modules={[Autoplay]}
              onSlideChange={handleSlideChange}
              breakpoints={{
                1300: {
                  slidesPerView: 2.5,
                  spaceBetween: 20,
                },
              }}
            >
              <SwiperSlide>
                <strong>Technology</strong>
                <span>기술 전문성</span>
                <p>
                  글로벌 CSP 3사의 최고 등급 파트너십을 획득한 <br />
                  전문가들과 함께합니다.
                </p>
              </SwiperSlide>
              <SwiperSlide>
                <strong>Networking</strong>
                <span>글로벌 네트워킹</span>
                <p>
                  글로벌 테크 기업과의 긴밀한 파트너십으로 <br />
                  최신 기술력을 업데이트합니다.
                </p>
              </SwiperSlide>
              <SwiperSlide>
                <strong>Knowhow</strong>
                <span>풍부한 노하우</span>
                <p>
                  금융,제조,유통,서비스,공공 등 <br />
                  다양한 산업 분야를 넘나듭니다.
                </p>
              </SwiperSlide>
            </Swiper>
          </div>
        </section>

        <section id="aboutContent3">
          <hgroup>
            <h2>
              <img src={awardSvg} alt="" />
              국내 MSP 파트너 유일 <br />
              글로벌 CSP 3사 <br />
              최고 등급 파트너십
            </h2>
            <p>
              국내 MSP 파트너 중 유일하게 글로벌 CSP 3사의 국내 최고 등급 파트너십을 <br />
              모두 획득하고 있으며, 더불어 GenAI, Migraion, Machine Learning 등 다양한 분야의 <br />
              역량을 인정받아 사업을 강화해 나가고 있습니다.
            </p>
          </hgroup>
          <div className="swiperWrap">
            <Swiper
              spaceBetween={10}
              slidesPerView={1.5}
              speed={800}
              breakpoints={{
                550: {
                  slidesPerView: 2.8,
                },
                768: {
                  slidesPerView: 3.8,
                },
                1024: {
                  slidesPerView: 4.3,
                },
                1300: {
                  slidesPerView: 5,
                },
                1600: {
                  slidesPerView: 5.3,
                },
                1800: {
                  slidesPerView: 6.2,
                },
              }}
            >
              {PartnersShipData.group1.map((item, index) => (
                <SwiperSlide key={item + index}>
                  <div>
                    <img src={`./assets/imgs/about/partnership/group1/`+item.image} alt={item.title} />
                  </div>
                  <span className="title">{item.title}</span>
                </SwiperSlide>
              ))}
            </Swiper>
            <div className="info">
              <span>
                AWS
                <mark>총 {PartnersShipData.group1.length}건</mark>
              </span>
              <ul>
                <li>4 AWS Competencies</li>
                <li>7 Partner Programs</li>
                <li>8 AWS Service Validations</li>
                <li>700+ AWS Certifications</li>
                <li>100+ AWS Customer Lanches</li>
              </ul>
            </div>
          </div>
          <div className="swiperWrap">
            <Swiper
              spaceBetween={10}
              slidesPerView={1.5}
              speed={800}
              breakpoints={{
                550: {
                  slidesPerView: 2.8,
                },
                768: {
                  slidesPerView: 3.8,
                },
                1024: {
                  slidesPerView: 4.3,
                },
                1600: {
                  slidesPerView: 5.3,
                },
                1800: {
                  slidesPerView: 6.2,
                },
              }}
            >
              {PartnersShipData.group2.map((item , idx) => (
                <SwiperSlide key={item + idx}>
                  <div>
                    <img src={`./assets/imgs/about/partnership/group2/`+item.image} alt={item.title} />
                  </div>
                  <span className="title">{item.title}</span>
                </SwiperSlide>
              ))}
            </Swiper>
            <div className="info">
              <span>
                GCP
                <mark>총 {PartnersShipData.group2.length}건</mark>
              </span>
            </div>
          </div>
          <div className="swiperWrap">
            <Swiper
              spaceBetween={10}
              slidesPerView={1.5}
              speed={800}
              breakpoints={{
                550: {
                  slidesPerView: 2.8,
                },
                768: {
                  slidesPerView: 3.8,
                },
                1024: {
                  slidesPerView: 4.3,
                },
                1600: {
                  slidesPerView: 5.3,
                },
                1800: {
                  slidesPerView: 6.2,
                },
              }}
            >
              {PartnersShipData.group3.map((item , index) => (
                <SwiperSlide key={item + index}>
                  <div>
                    <img src={`./assets/imgs/about/partnership/group3/`+item.image} alt={item.title} />
                  </div>
                  <span className="title">{item.title}</span>
                </SwiperSlide>
              ))}
            </Swiper>
            <div className="info">
              <span>
                MS
                <mark>총 {PartnersShipData.group3.length}건</mark>
              </span>
            </div>
          </div>
        </section>

        <section id="aboutContent4">
          <video src={aboutVideo} autoPlay muted loop playsInline></video>
        </section>
      </Page>
    </div>
  );
};

export default AboutPage;
